import http from '../../common/http.js'
export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			tips: '',
			// refCode: null,
			seconds: 10,
			btnStyle: {
				backgroundColor: '#667E86',
				fontSize: '32upx',
				color: '#fff',
				height: '90upx',
				border: 'none'
			},
			btnLoading: false,
		}
	},
	onLoad: function() {

		this.allRequest()
	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

		},
		aa: function() {
			uni.showLoading({
				title: '加载中'
			})
			let opts = {
				url: '',
				method: 'post'
			};
			let param = {
				city_id: '370700',
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		showToast: function(title, type, url, param) {
			this.$refs.uToast.show({
				title: title,
				type: type,
				url: url,
				param: param
			})
		},
		codeChange: function(text) {
			this.tips = text;
		},
		getCode() {
			if (this.$refs.uCode.canGetCode) {
				// 模拟向后端请求验证码
				uni.showLoading({
					title: '正在获取验证码'
				})
				setTimeout(() => {
					uni.hideLoading();
					this.showToast('发送成功', 'success')
					// 通知验证码组件内部开始倒计时
					this.$refs.uCode.start();
				}, 2000);
			} else {
				this.showToast('倒计时结束后再发送', 'error')
			}
		},
	}

}
